<!--
 * @Descripttion: 小项规则列表
 * @version: 
 * @Author: 曾元球
 * @Date: 2019-06-10 09:15:55
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-07-31 09:00:57
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{ name: 'BasicDataList' }">基础数据维护</el-breadcrumb-item>
        <el-breadcrumb-item>查看规则</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="head-btn"
        @click="openDialog('insert')">
        <img src="@/assets/img/add.png"
          alt="登记">&nbsp;<span>新增</span>
      </div>
    </div>

    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='scope'>
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <!-- <el-table-column label='规则'>
          <template></template>
        </el-table-column> -->
        <el-table-column label='小项名称'
          prop='projectName'
          show-overflow-tooltip></el-table-column>
        <template v-if="ids[1] == 1">
          <el-table-column label='范围下限'
            prop="lowerValue"
            width="100">
            <template v-slot="scope">
              {{scope.row.lowerValue | emptyData}}
            </template>
          </el-table-column>
          <el-table-column label='范围上限'
            prop='upperValue'
            width="100">
            <template v-slot="scope">
              {{scope.row.upperValue | emptyData}}
            </template>
          </el-table-column>
          <el-table-column label='年龄下限'
            prop='lowerAge'
            width="100">
            <template v-slot="scope">
              {{scope.row.lowerAge | emptyData}}
            </template>
          </el-table-column>
          <el-table-column label='年龄上限'
            prop='upperAge'
            width="100">
            <template v-slot="scope">
              {{scope.row.upperAge | emptyData}}
            </template>
          </el-table-column>
        </template>
        <el-table-column label='关键词'
          prop='kayword'
          show-overflow-tooltip>
          <template v-slot="scope">
            {{scope.row.kayword | emptyData}}
          </template>
        </el-table-column>
        <el-table-column label='检查结论'
          prop='checkConclusion'
          show-overflow-tooltip>
          <template v-slot="scope">
            {{scope.row.checkConclusion | emptyData}}
          </template>
        </el-table-column>
        <el-table-column label='诊断意见或印象'
          min-width='130px'
          prop='checkProposal'
          show-overflow-tooltip>
          <template v-slot="scope">
            {{scope.row.checkProposal | emptyData}}
          </template>
        </el-table-column>
        <el-table-column label='温馨提示'
          prop="checkWaring"
          show-overflow-tooltip>
          <template v-slot="scope">
            {{scope.row.checkWaring | emptyData}}
          </template>
        </el-table-column>
        <el-table-column label='操作'
          width="120">
          <template v-slot="scope">
            <i class="iconfont bj-search"
              @click="openDialog('see', scope.row)"></i>
            <i class="iconfont bj-edit"
              @click="openDialog('update', scope.row)"></i>
            <i class="iconfont bj-delete"
              @click="delConfirm(scope.row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination queryUrl='/sys/projectItemResult/listPaging'
        :queryObj='{projectId: ids[0]}'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref='pagination' />
    </div>

    <el-dialog :visible.sync="dialogIsShow"
      custom-class='my-dialog'
      class="flex"
      width='720px'
      :title="dialogTitles[dialogType]"
      :close-on-click-modal='false'
      center>
      <div class="dialog-name">项目名称：{{ids[2]}}</div>
      <template v-if='dialogType==="see"'>
        <el-form inline
          size="small"
          label-width="auto"
          label-position="left"
          key='1'>
          <el-row>
            <template v-if="ids[1] == 1">
              <el-col :span='7'>
                <el-form-item label="范围下限：">
                  {{dataInfo.lowerValue | emptyData}}
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="范围上限：">
                  {{dataInfo.upperValue | emptyData}}
                </el-form-item>
              </el-col>
              <el-col :span='10'>
                <el-form-item label="是否标准：">
                  {{dataInfo.standardResult == 2 ? '是' : '否'}}
                  <!-- <el-checkbox>标准规则</el-checkbox> -->
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="年龄下限：">
                  {{dataInfo.lowerAge | emptyData}}
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="年龄上限：">
                  {{dataInfo.upperAge | emptyData}}
                </el-form-item>
              </el-col>
            </template>
            <el-col :span='10'>
              <el-form-item label="疾病类型：">
                {{
                  dataInfo.diseaseType == 2 ? '常见疾病' : 
                  dataInfo.diseaseType == 1 ? '正常' : '疾病'
                }}
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-key-word"></i>关键词</div>
              <el-form-item class="block-form-item">
                <p class="text">{{dataInfo.kayword | emptyData}}</p>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-msg"></i>检查结论</div>
              <el-form-item class="block-form-item">
                <p class="text">{{dataInfo.checkConclusion | emptyData}}</p>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-opinion"></i>诊断意见或印象</div>
              <el-form-item class="block-form-item">
                <p class="text">{{dataInfo.checkProposal | emptyData}}</p>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-tip"></i>温馨提示</div>
              <el-form-item class="block-form-item">
                <p class="text">{{dataInfo.checkWaring | emptyData}}</p>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-info"
            @click="openDialog('update')">修改</el-button>
        </div>
      </template>

      <template v-else>
        <el-form inline
          size="small"
          class="default-form-style"
          ref='form'
          :model="formFields"
          :rules='rules'
          key='2'>
          <el-row type='flex'
            class="flex-wrap">
            <template v-if="ids[1] == 1">
              <el-col :span='7'>
                <el-form-item label="范围下限："
                  prop="lowerValue">
                  <el-input placeholder="请输入"
                    :value="formFields.lowerValue"
                    @input="$outNegative($event, 'lowerValue', formFields)"
                    style='width:100px'
                    maxlength="7"></el-input>
                  <!-- onblur='value=value?+value:""' -->
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="范围上限："
                  prop="upperValue">
                  <el-input placeholder="请输入"
                    :value="formFields.upperValue"
                    style='width:100px'
                    @input="$outNegative($event, 'upperValue', formFields)"
                    maxlength="7"></el-input>
                  <!-- onblur='value=value?+value:""' -->
                </el-form-item>
              </el-col>

              <el-col :span='10'>
                <el-form-item prop='standardResult'>
                  <el-checkbox v-model="formFields.standardResult"
                    class="red"
                    true-label='2'
                    false-label="1">标准规则</el-checkbox>
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="年龄下限："
                  prop="lowerAge">
                  <el-input placeholder="请输入"
                    :value="formFields.lowerAge"
                    @input="$outInt($event, 'lowerAge', formFields)"
                    maxlength="3"
                    style="width:100px" />
                </el-form-item>
              </el-col>
              <el-col :span='7'>
                <el-form-item label="年龄上限："
                  prop="upperAge">
                  <el-input placeholder="请输入"
                    :value="formFields.upperAge"
                    @input="$outInt($event, 'upperAge', formFields)"
                    maxlength="3"
                    style="width:100px" />
                </el-form-item>
              </el-col>
            </template>
            <el-col :span='10'>
              <el-form-item prop='diseaseType'
                label="疾病类型：">
                <el-select v-model='formFields.diseaseType'>
                  <el-option value="1"
                    label="正常"></el-option>
                  <el-option value="2"
                    label="常见疾病"></el-option>
                  <el-option value="3"
                    label="疾病"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label">
                <i class="iconfont bj-key-word"></i>
                关键词
                &nbsp;&nbsp;
                <span style="font-size:13px;color:#C0C4CC;font-weight:400">
                  多个关键字时，请用英文 [ ] 表示，举例：
                  {{ids[1] == 1 ? '[++]':'[ 胆囊 ] [ 肺气重 ]'}}
                </span>
              </div>
              <el-form-item class="block-form-item"
                prop="kayword">
                <el-input type="textarea"
                  v-model="formFields.kayword"
                  rows='4'
                  resize='none'
                  :maxlength="ids[1]==1?30:200"
                  show-word-limit></el-input>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-msg"></i>检查结论</div>
              <el-form-item class="block-form-item"
                prop="checkConclusion">
                <el-input type="textarea"
                  v-model="formFields.checkConclusion"
                  rows='4'
                  resize='none'
                  maxlength="300"
                  show-word-limit></el-input>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-opinion"></i>诊断意见或印象</div>
              <el-form-item class="block-form-item"
                prop="checkProposal">
                <el-input type="textarea"
                  v-model="formFields.checkProposal"
                  rows='4'
                  resize='none'
                  maxlength="300"
                  show-word-limit></el-input>
              </el-form-item>
            </el-col>
            <el-col>
              <div class="label"><i class="iconfont bj-tip"></i>温馨提示</div>
              <el-form-item class="block-form-item"
                prop="checkWaring">
                <el-input type="textarea"
                  v-model="formFields.checkWaring"
                  rows='4'
                  resize='none'
                  maxlength="300"
                  show-word-limit></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <div slot='footer'>
          <el-button class="btn-hollowing"
            @click="dialogIsShow = false">取消</el-button>
          <el-button class="btn-info"
            @click="onSubmit"
            :loading="submitLoading">保存</el-button>
        </div>
      </template>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: 'RuleList',
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data() {
    // this 当前实例
    return {
      tableData: [],
      formFields: {
        projectId: '',
        resultType: '',
        upperValue: '',
        lowerValue: '',
        lowerAge: '',
        upperAge: '',
        diseaseType: '',
        kayword: '',
        checkConclusion: '',
        checkProposal: '',
        checkWaring: '',
        standardResult: ''
      },
      rules: {
        // kayword: {max: 30, message: '最多输入30个字符'},
        // checkConclusion: {max: 300, message: '最多输入300个字符'},
        // checkProposal: {max: 300, message: '最多输入300个字符'},
        // checkWaring: {max: 300, message: '最多输入300个字符'}
        lowerAge: {
          validator: (rule, val, cb) => {
            if (!val || !this.formFields.upperAge) return cb()
            let lower = +val,
              upper = +this.formFields.upperAge
            if (lower > upper) return cb(new Error('不能大于上限'))
            this.$refs.form.clearValidate('upperAge')
            return cb()
          },
          trigger: 'blur'
        },
        upperAge: {
          validator: (rule, val, cb) => {
            if (!val || !this.formFields.lowerAge) return cb()
            let upper = +val,
              lower = +this.formFields.lowerAge
            if (lower > upper) return cb(new Error('不能小于下限'))
            this.$refs.form.clearValidate('lowerAge')
            return cb()
          },
          trigger: 'blur'
        },
        upperValue: {
          validator: (rule, val, cb) => {
            if (!val || !this.formFields.lowerValue) return cb()
            let upper = +val,
              lower = +this.formFields.lowerValue
            if (lower > upper) return cb(new Error('不能小于下限'))
            this.$refs.form.clearValidate('lowerValue')
            return cb()
          },
          trigger: 'blur'
        },
        lowerValue: {
          validator: (rule, val, cb) => {
            if (!val || !this.formFields.upperValue) return cb()
            let lower = +val,
              upper = +this.formFields.upperValue
            if (lower > upper) return cb(new Error('不能大于上限'))
            this.$refs.form.clearValidate('upperValue')
            return cb()
          },
          trigger: 'blur'
        }
      },
      pageNum: 1,
      pageSize: 10,
      dataInfo: {},
      dialogIsShow: false,
      dialogType: '',
      loading: true,
      submitLoading: false
    }
  },
  computed: {
    ids() {
      return this.id.split('&')
    }
  },
  created() {
    this.dialogTitles = { see: '查看', insert: '新增', update: '修改' }
  },

  methods: {
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    delConfirm({ sysGuid }) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/projectItemResult/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.pagination.initPageList(pageNum)
              } else {
                this.$refs.pagination.initPageList()
              }
            })
            .catch(err => {
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },

    openDialog(type, data) {
      this.dialogIsShow = true
      this.dialogType = type
      if (data) {
        this.dataInfo = data
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = data[key]
        })
        this.formFields.sysGuid = data.sysGuid
      } else if (type === 'insert') {
        delete this.formFields.sysGuid
        Object.keys(this.formFields).forEach(key => {
          this.formFields[key] = ''
        })
        this.formFields.diseaseType = '1' // 默认值：1为正常
      }
      this.$nextTick(_ => {
        this.dialogType !== 'see' && this.$refs.form.clearValidate()
      })
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (!valid) return
        this.submitLoading = true
        this.formFields.projectId = this.ids[0]
        this.formFields.resultType = this.ids[1]
        let url = `/sys/projectItemResult/${this.dialogType}`
        let msg = this.dialogTitles[this.dialogType]
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.dialogIsShow = false
            this.$refs.pagination.initPageList()
            this.$message.success(`${msg}成功`)
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.table-box {
  padding-top: 20px;
}
.ipt {
  vertical-align: top;
  /deep/ .el-input__inner {
    vertical-align: top;
  }
}
.dialog-name {
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 700;
}
.label {
  margin-bottom: 18px;
  font-size: 16px;
  font-weight: 700;
  color: #666;
  .iconfont {
    margin-right: 12px;
    font-size: 18px;
    font-weight: 400;
    color: @color-red;
  }
}
p.text {
  padding-bottom: 10px;
  text-indent: 2em;
}
</style>


